<template>
 
</template>
<script>
export default {
    mounted:function(){
         var _this = this;
    //console.log(this);
    
    getRequest("/currentUserName").then(
      function (msg) {
        _this.currentUserName = msg.data;
        _this.$router.replace({ path: "/home/"+_this.currentUserName +"/listArticle/allarticles/1" });
      },
      function (msg) {
        _this.$alert("请先登录", "您当前是游客", {
          confirmButtonText: "确定",
          type: "warning",
        });
        _this.currentUserName = "游客";
        _this.$router.replace({ path: "/" });
      }
    );
    },
}
</script>


