<template>
  <div>
    <div class="article-show">
      <div class="article-title">{{currArticle.title}}</div>
<div class="article-tagss">
      <div class="article-tags" v-for="(item,index) in currArticle.tags" :key="index">
        <el-tag>{{item.tagName}}</el-tag>&nbsp;&nbsp;
      </div>  </div>
      <div
        class="article-time"
      >浏览量：{{currArticle.pageView}}&nbsp;&nbsp;&nbsp;&nbsp;作者：{{author}}&nbsp;&nbsp;&nbsp;&nbsp;发表日期：{{publishdate}}</div>
      <br />
      <span class="currarticle" v-html="currArticle.htmlContent"></span>
    </div>
    <br />
    <div class="article-show-rate">
      大众评分：
      <el-rate v-model="rates" disabled show-score text-color="#ff9900" score-template="{value}"></el-rate>我的评分：
      <el-rate v-model="myrate" :colors="colors" @change="submitRate"></el-rate>
    </div>

    <div style="text-align:left">评论：</div>
    <ul v-for="item in comments" :key="item.id">
      <li>
        <div class="article-show-comment">
          <div>
            <span
              class="article-show-author"
              v-if="item.reply=='YIKINGYA'"
              @click="changeButton2(item.author)"
            >{{item.author}}</span>
            <span
              class="article-show-author"
              v-if="item.reply!='YIKINGYA'"
              @click="changeButton2(item.author)"
            >
              {{item.author}}
              <font size="2" color="#20a0ff">回复：</font>
              {{item.reply}}
            </span>
            :{{item.comment}}&nbsp;&nbsp;&nbsp;&nbsp;
          </div>
          <div class="article-time">{{item.publishDate}}</div>
        </div>
      </li>
    </ul>

    <div class="article-comment">
      <el-input
        type="textarea"
        :autosize="{ minRows: 4, maxRows: 4}"
        placeholder="发表评论"
        v-model="comment"
        :maxlength="500"
        show-word-limit
      ></el-input>
    </div>
    <div class="article-comment-button">
      <el-input
        v-model="reply"
        style="width:200px"
        placeholder="回复(点击/输入用户名)"
        @focus="changeButton"
      ></el-input>
      <el-button type="danger" @click="saveComment">{{commentbutton}}</el-button>
    </div>
  </div>
</template>
<script>
import { getRequest } from "../utils/api";
import { postRequest } from "../utils/api";
export default {
  mounted: function () {
    var _this = this;
    getRequest("/article/currarticle/" + this.$route.params.currid).then(
      function (msg) {
        _this.publishdate = new Date(msg.data.publishDate).toLocaleDateString();
        _this.currArticle = msg.data;
        _this.author = _this.$route.params.author;
        //console.log(msg.data);
      },
      function (err) {}
    );
    getRequest("/comments/listcomments/" + _this.$route.params.currid).then(
      function (res) {
        //console.log(res);
        _this.comments = res.data;
      }
    );
    postRequest("/rate/myrate/", {
      aid: _this.$route.params.currid,
      username: _this.$route.params.username,
    }).then(function (res) {
     // console.log(res);
      _this.myrate = res.data.rate;
      _this.rates = res.data.rates;
    });
    _this.currentUserName = _this.$route.params.username;
  },
  methods: {
    submitRate: function (rate) {
      var _this = this;
      //console.log(rate);
      postRequest("/rate/addrate", {
        aid: _this.currArticle.id,
        username: _this.$route.params.username,
        rate: rate,
      }).then((resp) => {
       // console.log(resp);
      });
    },
    changeButton: function () {
      this.commentbutton = "回  复";
    },
    changeButton2: function (author) {
      if (author != this.currentUserName) {
        this.reply = author;
        this.commentbutton = "回  复";
      }
    },
    saveComment: function () {
      var that = this;
      if (this.comment == "") {
        this.$message({ type: "error", message: "数据不能为空!" });
        return;
      }
      var _this = this;
      _this.loading = true;

      postRequest("/comments/addcomment", {
        id: -1,
        username: _this.currentUserName,
        aid: _this.currArticle.id,
        content: _this.comment,
        replyName: _this.reply,
      }).then((resp) => {
        getRequest("/comments/listcomments/" + _this.$route.params.currid).then(
          function (res) {
            _this.comments = res.data;
            //console.log(_this.comments);
            _this.comment = "";
            _this.commentbutton = "发表评论";
          }
        );
      });
    },
  },

  data() {
    return {
      currArticle: [],
      publishdate: "",
      author: "",
      comment: "",
      comments: [],
      commentbutton: "发表评论",
      reply: "",
      currentUserName: "",
      value: null,
      rates: 0,
      myrate: 0,
      iconClasses: ["icon-rate-face-1", "icon-rate-face-2", "icon-rate-face-3"],
      colors: ["#99A9BF", "#F7BA2A", "#FF9900"],
    };
  },
};
</script>
<style>
ul {
  list-style: none;
  margin-left: -40px;
}
.currarticle {
  text-align: left;
}
.article-show {
  overflow-y: hidden;
  overflow-x: scroll;
  width: 80%;
  padding: 30px;
  box-shadow: rgb(194, 193, 193) 0px 1px 3px 3px;
  border-radius: 5px;
}
.article-show-comment {
  margin-top: 15px;
  width: 82.5%;
  margin-left: 0px;
  padding: 15px;

  padding-bottom: 15px;
  text-align: left;
  box-shadow: rgb(194, 193, 193) 0px 1px 3px 3px;
  border-radius: 5px;
}
.article-title {
  font-size: 18px;
  width: 100%;
  text-align: center;
  color: #20a0ff;
}
.article-show-author {
  font-size: 18px;
  text-align: left;
  color: #f56c6c;
}
.article-time {
  font-size: 13px;
  width: 100%;
  text-align: right;
  color: #f56c6c;
}
.article-comment {
  margin-top: 15px;
  font-size: 13px;
  width: 85%;

  height: 95px;
  text-align: left;
  border-radius: 2px;
  -webkit-box-shadow: #409eff 0px 0px 5px;
  -moz-box-shadow: #409eff 0px 0px 5px;
  box-shadow: #409eff 0px 0px 5px;
}
.article-comment-button {
  margin-top: 10px;
  position: absolute;
  left: 65%;
  margin-bottom: 50px;
}
.article-show-rate {
  width: 100%;
  text-align: left;
}
.article-tags {
  display: inline-block;
}
.article-tagss {
  text-align: right;
  width: 100%;
}
</style>


