<template>
  <el-form
    :rules="rules"
    class="login-container"
    label-position="left"
    label-width="0px"
    v-loading="loading"
  >
    <h3 class="login_title">用户注册</h3>
    <el-form-item prop="account">
      用户名：<el-input type="text" v-model="loginForm.username" auto-complete="off" placeholder="请输入用户名" style="width:80%"></el-input>
    </el-form-item>
    <el-form-item prop="checkPass">
      密&nbsp;&nbsp;码：   <el-input
        type="password"
        style="width:80%"
        v-model="loginForm.password"
        auto-complete="off"
        placeholder="请输入密码"
      ></el-input>
    </el-form-item>
    <el-form-item prop="checkEmail">
      邮&nbsp;&nbsp;箱： <el-input type="text" style="width:80%" v-model="loginForm.email" auto-complete="off" placeholder="请输入邮箱"></el-input>
    </el-form-item>
    <el-form-item style="width: 100%">
      <el-button type="primary" @click.native.prevent="submitClick" style="width: 100%">注册</el-button>
    </el-form-item>
    <el-form-item style="width: 100%"></el-form-item>
  </el-form>
</template>
<script>
import { postRequest } from "../utils/api";
import { putRequest } from "../utils/api";
export default {
  data() {
    return {
      rules: {
        account: [{ required: true, message: "请输入用户名", trigger: "blur" }],
        checkPass: [{ required: true, message: "请输入密码", trigger: "blur" }],
        checkEmail: [{ required: true, message: "请输入邮箱", trigger: "blur" }],
      },
      checked: true,
      loginForm: {
        username: "",
        password: "123",
        email:"yiking@qq.com",
      },
      loading: false,
    };
  },
  methods: {
    submitClick: function () {
      var _this = this;
      this.loading = true;
      postRequest("/reguser", {
        username: this.loginForm.username,
        password: this.loginForm.password,
        email:this.loginForm.email,
      }).then(
        (resp) => {
          _this.loading = false;
          // console.log(resp);
          if (resp.status == 200) {
            //成功
            var json = resp.data;
            console.log(json);
            if (json.status == "success") {
              _this.$router.replace({ path: "/" });
            } else {
              _this.$alert("注册失败!", json.msg[0]);
            }
          } else {
            //失败
            _this.$alert("注册失败!", "失败!");
          }
        },
        (resp) => {
          _this.loading = false;
          _this.$alert("找不到服务器⊙﹏⊙∥!", "失败!");
        }
      );
    },
  },
};
</script>
<style>
.login-container {
  border-radius: 15px;
  background-clip: padding-box;
  margin: 150px auto;
  width: 350px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}

.login_title {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}

.login_remember {
  margin: 0px 0px 35px 0px;
  text-align: left;
}
</style>



