<template>
  <div>
    

    <div class="articlelist-wordcloud" v-show="isShowWordCloud">
      <wordcloud
        :data="defaultWords"
        nameKey="name"
        valueKey="value"
        :color="myColors"
        :fontSize="fontSizeWord"
        :showTooltip="false"
        :wordClick="wordClickHandler"
      ></wordcloud>
    </div>

    <!-- <word-cloud-chart
        id="echarts05"
        :title="title"
        :data="echartscloud"
    />-->

    <div class="articlelist">
      <div v-show="isSearch" class="articlelist-search">
        <el-input v-model="searchCondition" style="width:200px" placeholder="查询"></el-input>
        <el-button type="primary" @click="searchArticle" icon="el-icon-search">搜索</el-button>
        <el-select
          v-model="sort"
          clearable
          placeholder="排序方式"
          style="width:115px"
          @change="sortArticle"
          v-show="!orSearch"
          @clear="clearSort1"
        >
          <el-option
            v-for="item in sortoptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
        <el-select
          v-model="sort2"
          clearable
          placeholder="排序"
          style="width:115px"
          @change="sortArticle2"
          v-show="orSearch"
          @clear="clearSort2"
        >
          <el-option
            v-for="item in sortoptions2"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </div>
      <ul v-for="item in articles" :key="item.id">
        <li @click="showarticle(item.id,item.author)">
          <div class="listarticle">
            <div class="artheader">{{item.title}}</div>
            <div class="artcontent">{{item.summary}}</div>
            <el-popover
              class="item1"
              placement="bottom-end"
              width="200"
              @show="getauthor(item.author)"
              trigger="hover"
              :content="usertips"
              style=" white-space:pre-wrap"
            >
              <div
                class="arttime"
                slot="reference"
              >浏览量：{{item.pageview}}&nbsp;&nbsp;&nbsp;&nbsp;作者：{{item.author}}&nbsp;&nbsp;&nbsp;&nbsp;日期：{{item.publishDate}}</div>
            </el-popover>
          </div>
        </li>
      </ul>
      <el-pagination
        background
        layout="prev, pager, next"
        :total="totalSize"
        :current-page="currpage"
        :page-size="6"
        @current-change="pageChang"
        v-show="!ismyarticle"
      ></el-pagination>
      <div>copyright©YIKING</div>
    </div>
  </div>
</template>
<script>
import { getRequest } from "../utils/api";
import wordcloud from "vue-wordcloud";

export default {
  components: {
    wordcloud,
  },
  mounted: function () {
    var _this = this;
    getRequest("/article/getsearchconditionsum").then(function (msg) {
     // console.log(msg.data);
      _this.defaultWords = msg.data;
    });
    if (_this.$route.params.page != 1) {
      _this.startPage = _this.$route.params.page;
      _this.currpage = _this.startPage / 6 + 1;
     // console.log(_this.startPage);
    }
    if (_this.$route.params.article == "allarticles") {
      getRequest("/article/allarticles/" + _this.startPage).then(
        function (msg) {
         // console.log(msg);
          _this.articles = msg.data.articles;
          _this.totalSize = msg.data.totalSize;
          _this.ismyarticle = false;
          _this.isSearch = true;
        },
        function (msg) {
          _this.articles = "出错啦！！";
        }
      );
    } else if (_this.$route.params.article.substring(0, 6)=="yiking"){
      var id=_this.$route.params.article.substring(6);
      getRequest("/article/getcatory/" + id).then(
        function (msg) {
          _this.articles = msg.data.articles;
          _this.ismyarticle = true;
          _this.isSearch = false;
          _this.isShowWordCloud = true;
        },)

    }
     else if (_this.$route.params.article == "myarticles") {
      getRequest("/article/myarticles/" + _this.$route.params.username).then(
        function (msg) {
          _this.articles = msg.data.articles;
          _this.ismyarticle = true;
          _this.isSearch = false;
          _this.isShowWordCloud = false;
        },
        function (msg) {
          _this.articles = "出错啦！！";
        }
      );
    } else if (_this.$route.params.article == "articlesorderbypublishdate") {
      getRequest("/article/articlesorderbypublishdate/" + _this.startPage).then(
        function (msg) {
        //  console.log(msg);
          _this.articles = msg.data.articles;
          _this.ismyarticle = false;
          _this.totalSize = msg.data.totalSize;
          _this.sort = "发表日期";
          _this.pagehelper = "articlesorderbypublishdate";
        },
        function (msg) {
          _this.articles = "出错啦！！";
        }
      );
    } else if (_this.$route.params.article == "articlesorderbycomments") {
      getRequest("/article/articlesorderbycomments/" + _this.startPage).then(
        function (msg) {
          //console.log(msg);
          _this.articles = msg.data.articles;
          _this.ismyarticle = false;
          _this.totalSize = msg.data.totalSize;

          _this.pagehelper = "articlesorderbycomments";
          _this.sort = "评论数量";
        },
        function (msg) {
          _this.articles = "出错啦！！";
        }
      );
    } else if (_this.$route.params.article == "articlesorderbyrates") {
      getRequest("/article/articlesorderbyrates/" + _this.startPage).then(
        function (msg) {
         // console.log(msg);
          _this.articles = msg.data.articles;
          _this.ismyarticle = false;
          _this.sort = "大众评分";
          _this.totalSize = msg.data.totalSize;
          _this.pagehelper = "articlesorderbyrates";
        },
        function (msg) {
          _this.articles = "出错啦！！";
        }
      );
    } else if (_this.$route.params.article == "search") {
      getRequest("/article/search/" + _this.$route.params.search).then(
        function (msg) {
         // console.log(msg);
          _this.articles = msg.data.articles;
          _this.isSearch = true;
          _this.orSearch = true;
          _this.ismyarticle = true;
        },
        function (msg) {
          _this.articles = "出错啦！！";
        }
      );
    } else if (
      _this.$route.params.article == "searcharticlesorderbypublishdate"
    ) {
      getRequest(
        "/article/searcharticlesorderbypublishdate/" +
          _this.$route.params.search
      ).then(
        function (msg) {
          //console.log(msg);
          _this.articles = msg.data.articles;
          _this.isSearch = true;
          _this.orSearch = true;
          _this.ismyarticle = true;
          _this.sort2 = "发表时间";
        },
        function (msg) {
          _this.articles = "出错啦！！";
        }
      );
    } else if (_this.$route.params.article == "searcharticlesorderbyrates") {
      getRequest(
        "/article/searcharticlesorderbyrates/" + _this.$route.params.search
      ).then(
        function (msg) {
         // console.log(msg);
          _this.articles = msg.data.articles;
          _this.isSearch = true;
          _this.orSearch = true;
          _this.ismyarticle = true;
          _this.sort2 = "大众评分";
        },
        function (msg) {
          _this.articles = "出错啦！！";
        }
      );
    } else if (_this.$route.params.article == "searcharticlesorderbycomments") {
      getRequest(
        "/article/searcharticlesorderbycomments/" + _this.$route.params.search
      ).then(
        function (msg) {
         // console.log(msg);
          _this.articles = msg.data.articles;
          _this.isSearch = true;
          _this.orSearch = true;
          _this.ismyarticle = true;
          _this.sort2 = "评论数量";
        },
        function (msg) {
          _this.articles = "出错啦！！";
        }
      );
    }
  },
  methods: {
    getauthor(author) {
      var _this = this;
      getRequest("/article/getauthordata/" + author).then(function (msg) {
        
        _this.usertips =
          "☺文章总数:" +
          msg.data.articlesum +
          "☺文章总积分:" +
          msg.data.articlesrate +
          "☹文章总体重复度:" +
          msg.data.userrepeatrate;
      });
    },
    wordClickHandler(name, value, vm) {
      var _this = this;
      this.$router.push({
        path:
          "/home/" +
          _this.$route.params.username +
          "/listArticle/search/" +
          name +
          "/1",
      });
    },
    clearSort1: function () {
      var _this = this;
      _this.$router.push({
        path:
          "/home/" +
          _this.$route.params.username +
          "/listArticle/allarticles/1/0",
      });
    },
    clearSort2: function () {
      var _this = this;
      _this.$router.push({
        path:
          "/home/" +
          _this.$route.params.username +
          "/listArticle/search/" +
          _this.$route.params.search +
          "/1",
      });
    },
    sortArticle: function (sort) {
      var _this = this;

      if (sort == 1) {
        _this.$router.push({
          path:
            "/home/" +
            _this.$route.params.username +
            "/listArticle/articlesorderbypublishdate/0/1",
        });
        getRequest("/article/articlesorderbypublishdate/0").then(
          function (msg) {
           // console.log(msg);
            _this.articles = msg.data.articles;
            _this.ismyarticle = false;
            _this.sort = "发表日期";
            _this.totalSize = msg.data.totalSize;
            _this.pagehelper = "articlesorderbypublishdate";
          },
          function (msg) {
            _this.articles = "出错啦！！";
          }
        );
      } else if (sort == 2) {
        _this.$router.push({
          path:
            "/home/" +
            _this.$route.params.username +
            "/listArticle/articlesorderbyrates/0/1",
        });
        getRequest("/article/articlesorderbyrates/0").then(
          function (msg) {
            //console.log(msg);
            _this.articles = msg.data.articles;
            _this.ismyarticle = false;
            _this.totalSize = msg.data.totalSize;
            _this.sort = "大众评分";
            _this.pagehelper = "articlesorderbyrates";
          },
          function (msg) {
            _this.articles = "出错啦！！";
          }
        );
      } else if (sort == 3) {
        _this.$router.push({
          path:
            "/home/" +
            _this.$route.params.username +
            "/listArticle/articlesorderbycomments/0/1",
        });
        getRequest("/article/articlesorderbycomments/0").then(
          function (msg) {
            //console.log(msg);
            _this.articles = msg.data.articles;
            _this.ismyarticle = false;
            _this.totalSize = msg.data.totalSize;
            _this.sort = "评论数量";
            _this.pagehelper = "articlesorderbycomments";
          },
          function (msg) {
            _this.articles = "出错啦！！";
          }
        );
      }
    },
    sortArticle2: function (sort) {
      var _this = this;

      if (sort == 1) {
        _this.$router.push({
          path:
            "/home/" +
            _this.$route.params.username +
            "/listArticle/searcharticlesorderbypublishdate/" +
            _this.$route.params.search +
            "/1",
        });
        getRequest(
          "/article/searcharticlesorderbypublishdate/" +
            _this.$route.params.search
        ).then(
          function (msg) {
            //console.log(msg);
            _this.articles = msg.data.articles;
            _this.ismyarticle = true;
            _this.sort2 = "发表日期";
            _this.totalSize = msg.data.totalSize;
            _this.pagehelper = "articlesorderbypublishdate";
          },
          function (msg) {
            _this.articles = "出错啦！！";
          }
        );
      } else if (sort == 2) {
        _this.$router.push({
          path:
            "/home/" +
            _this.$route.params.username +
            "/listArticle/searcharticlesorderbyrates/" +
            _this.$route.params.search +
            "/1",
        });
        getRequest(
          "/article/searcharticlesorderbyrates/" + _this.$route.params.search
        ).then(
          function (msg) {
            //console.log(msg);
            _this.articles = msg.data.articles;
            _this.ismyarticle = true;
            _this.totalSize = msg.data.totalSize;
            _this.sort = "大众评分";
            _this.pagehelper = "searcharticlesorderbyrates";
          },
          function (msg) {
            _this.articles = "出错啦！！";
          }
        );
      } else if (sort == 3) {
        _this.$router.push({
          path:
            "/home/" +
            _this.$route.params.username +
            "/listArticle/searcharticlesorderbycomments/" +
            _this.$route.params.search +
            "/1",
        });
        getRequest(
          "/article/searcharticlesorderbycomments/" + _this.$route.params.search
        ).then(
          function (msg) {
           // console.log(msg);
            _this.articles = msg.data.articles;
            _this.ismyarticle = true;
            _this.totalSize = msg.data.totalSize;
            _this.sort = "评论数量";
            _this.pagehelper = "searcharticlesorderbycomments";
          },
          function (msg) {
            _this.articles = "出错啦！！";
          }
        );
      }
    },
    searchArticle: function () {
      var _this = this;

      if (_this.searchCondition != "") {
        _this.orSearch = true;
        _this.$router.push({
          path:
            "/home/" +
            _this.$route.params.username +
            "/listArticle/search/" +
            _this.searchCondition +
            "/1",
        });
        getRequest("/article/search/" + _this.searchCondition).then(
          function (msg) {
           // console.log(msg);
            _this.articles = msg.data.articles;
            _this.isSearch = true;
            _this.ismyarticle = true;
          },
          function (msg) {
            _this.articles = "出错啦！！";
          }
        );
      }
    },
    pageChang: function (page) {
      var _this = this;
      _this.startPage = page * 6 - 6;
      getRequest("/article/" + _this.pagehelper + "/" + _this.startPage).then(
        function (msg) {
         // console.log(msg);
          _this.articles = msg.data.articles;
          _this.ismyarticle = false;
          _this.$router.push({
            path:
              "/home/" +
              _this.$route.params.username +
              "/listArticle/" +
              _this.$route.params.article +
              "/0/" +
              _this.startPage,
          });
        },
        function (msg) {
          _this.articles = "出错啦！！";
        }
      );
    },
    showarticle: function (id, author) {
      var _this = this;

      _this.$router.push({
        path:
          "/home/" +
          _this.$route.params.username +
          "/listArticle/article/" +
          id +
          "/" +
          author +
          "/" +
          _this.startPage,
      });
    },
    handleClick(tab, event) {
      //console.log(tab, event);
    },
  },
  data() {
    return {
     
      articles: [],
      totalSize: 100,
      startPage: 0,
      currpage: 1,
      ismyarticle: "false",
      isSearch: true,
      pagehelper: "allarticles",
      searchCondition: "",
      orSearch: false,
      usertips: "",
      sortoptions: [
        {
          value: "1",
          label: "发表时间",
        },
        {
          value: "2",
          label: "大众评分",
        },
        {
          value: "3",
          label: "评论数量",
        },
      ],
      isShowWordCloud: true,
      sort: "",
      sortoptions2: [
        {
          value: "1",
          label: "发表时间",
        },
        {
          value: "2",
          label: "大众评分",
        },
        {
          value: "3",
          label: "评论数量",
        },
      ],
      fontSizeWord: [10, 50],
      sort2: "",
      myColors: ["#409EFF", "#67C23A", "#E6A23C", "#F56C6C", "#909399"],
      defaultWords: [],
    };
  },
};
</script>
<style>
ul {
  list-style: none;
}
.listarticle {
  width: 1000px;
  padding: 15px;
  height: 60px;
  -webkit-box-shadow: #666 0px 0px 1px;
  -moz-box-shadow: #666 0px 0px 1px;
  box-shadow: #c0c4cc 0px 1px 1px 1px;
  border-radius: 5px;
}
.articlelist-search {
  width: 1000px;
  text-align: center;
}
.listarticle:hover {
  background: #f2f6fc;
}
.artheader {
  color: #20a0ff;
  width: 100%;
  text-align: center;
  font-size: 18px;
}
.arttime {
  width: 100%;
  text-align: right;
  margin-top: -10px;
  font-size: 13px;
  color: #f56c6c;
}
.articlelist {
  position: absolute;
  top: 70px;
}
.artcontent {
  width: 100%;
  text-align: left;
  font-size: 15px;
}
.articlelist-wordcloud {
  width: 20%;
  margin-left: 80%;
  -webkit-box-shadow: #666 0px 0px 1px;
  -moz-box-shadow: #666 0px 0px 1px;
  box-shadow: #c0c4cc 0px 1px 1px 1px;
  border-radius: 2px;
}
.item1 {
  margin-top: 10px;
  margin-left: 550px;
}

</style>


