<template>

        <div class="bodyimage" :style="beautifulgril">
            <div @click="changeImg1" style="position: absolute; left: 1470px;top:710px;">
                《
            </div>
                   
                    <div  @click="changeImg2" style="position: absolute; left: 1500px;top: 710px;">></div>

                    
                    <div class="player">
                        <el-row>

                            <el-col :span="24">
                                <div class="grid-content ">
                                    <hr>
                                    
                                   
                                   <el-input v-model="song"
                                        style="width: 200px;height: 30px; " placeholder="请输入歌名/歌手"
                                        @keyup.native.enter="getMusic"></el-input>
                                    <el-button type="danger" @click="getMusic">查询歌曲</el-button>
                                    <hr>
                                </div>
                            </el-col>
                        </el-row>


                        <el-row class="ul-list">

                            <el-col :span="8">

                                <div class="playlist">
                                    <hr>
                                    <hr>
                                    <ul>
                                        <li v-for="(item,index) in songlist" :key="index">
                                            <el-row>
                                                <el-col :span="12">
                                                    <el-tag type="success">{{item.name}}</el-tag>
                                                </el-col>
                                                <el-col :span="6">
                                                    <el-tag type="warning">{{songer[index]}}</el-tag>
                                                </el-col>
                                                <el-col :span="6">
                                                    <el-tag type="primary" @click="playMusic(item.id,item.name,index)">
                                                        播放
                                                    </el-tag>
                                                </el-col>


                                            </el-row>

                                            <hr>
                                        </li>
                                    </ul>
                                </div>
                            </el-col>
                            <el-col :span="8">
                                <div class="playlist">
                                    <div class="demo">

                                        <img v-show="singerurl!=''" class="an img" :src="singerurl" width="250"
                                            height="250" />

                                    </div>
                                    <hr>
                                    <el-tag type="danger" v-show="currsong!=''"
                                        style="top: 315px; position: absolute;  left: 340px;"
                                        @click="playMusic(songlist[currindex-1==-1?songlist.length-1:currindex-1].id,songlist[currindex-1==-1?songlist.length-1:currindex-1].name,currindex-1==-1?songlist.length-1:currindex-1)">
                                        《 </el-tag>
                                            <el-tag type="danger" v-show="currsong!=''"
                                                style="top: 315px; position: absolute;  left: 400px;">{{ currsong}}
                                            </el-tag>
                                            <el-tag type="danger" v-show="currsong!=''"
                                                style="top: 315px; position: absolute;  left: 520px; "
                                                @click="playMusic(songlist[currindex+1==songlist.length?0:currindex+1].id,songlist[currindex+1==songlist.length?0:currindex+1].name,currindex+1==songlist.length?0:currindex+1)">
                                               》</el-tag>
                                            <audio id="myaudio"
                                                style="top: 355px; position: absolute; width: 250px; left: 330px;"
                                                :src="songurl" controls="controls" autoplay="autoplay" ref="mymusic"
                                                @ended="isplay()"></audio>
                                            <hr>

                                </div>
                            </el-col>
                            
                            <el-col :span="8">
                                <div class="playlist">
                                    <hr>
                                    <hr>
                                    <ul>
                                        <li v-for="(item,index) in hotcomments" :key="index">
                                            <img :src="item.user.avatarUrl" alt="" class="head-img">
                                            <span>{{item.user.nickname}}</span>

                                            <div class="comment">{{item.content}}</div>

                                            <hr>
                                        </li>
                                    </ul>
                                </div>
                            </el-col>
                        </el-row>
                        <el-row>

                            <el-col :span="24">
                                <div class="grid-content">
                                    <hr>
                                    <hr>
                                </div>
                            </el-col>
                        </el-row>
                    </div>

                    <!-- <input id="musicflag" type="text" v-model="isplay">
<p>{{isplay}}</p> -->
        </div>
   
</template>
<script>
import { getRequest } from "../utils/api";
import { postRequest } from "../utils/api";
import { putRequest } from "../utils/api";
import { deleteRequest } from "../utils/api";
export default {
 name:"musicplayer",
  methods: {
    isplay: function () {

                //console.log(this.isplay)
                this.playMusic(this.songlist[this.currindex + 1 == this.songlist.length ? 0 : this.currindex + 1].id,
                    this.songlist[this.currindex + 1 == this.songlist.length ? 0 : this.currindex + 1].name,
                    this.currindex + 1 == this.songlist.length ? 0 : this.currindex + 1);
            },
           changeImg1:function(){
            var that = this;
            if(that.index==1){
                that.index=that.sonenum;
            }else{
                that.index--;
            }
            that.beautifulgril.background='url(D:/meinv/meinv'+that.index+'.jpg) no-repeat center center';
           },
           changeImg2:function(){
            var that = this;
            //var index=parseInt(that.beautifulgril.background.substring(18,20));
            if(that.index==that.sonenum){
                that.index=1;
            }else{
                that.index++;
            }
            that.beautifulgril.background='url(D:/meinv/meinv'+that.index+'.jpg) no-repeat center center';
           },
            getJoke: function () {
                var that = this;
                getRequest("https://autumnfish.cn/api/joke").then(function (response) {
                    that.joke = response.data;
                }, function (err) {

                })
            },
            getMusic: function () {
                var that = this;
               // console.log(this.$refs.mymusic.ended);
                 getRequest('https://autumnfish.cn/search?keywords=' + this.song).then(function (response) {
                   // console.log(response);
                    that.songlist = response.data.result.songs;
                    for (var i = 0; i < that.songlist.length; i++)
                        that.songer[i] = response.data.result.songs[i].artists[0].name;
                }, function (err) {

                })
            },
            playMusic: function (musicId, songname, index) {

                var that = this;
                this.currindex = index;
                this.currsong = songname;
                if (index == -1) {
                    this.currindex = that.singerurl.length - 1;
                }
                if (index == that.singerurl.length) {
                    this.currindex = 0;
                }

                //console.log(musicId);
                 getRequest('https://autumnfish.cn/song/url?id=' + musicId).then(function (response) {
                    that.songurl = response.data.data[0].url;

                   // console.log(response);
                }, function (err) {

                }),
                   getRequest('https://autumnfish.cn/song/detail?ids=' + musicId).then(function (response) {
                        that.singerurl = response.data.songs[0].al.picUrl;
                      //  console.log(response);
                    }, function (err) {

                    }),
                   getRequest('https://autumnfish.cn/comment/hot?type=0&id=' + musicId).then(function (response) {
                        that.hotcomments = response.data.hotComments;
                       // console.log(response.data.hotComments);
                    }, function (err) {
                    })
            },
  },
 
  mounted: function () {
    
  },
  data() {
    return {
     beautifulgril: {
                background: 'url(http://pic1.win4000.com/wallpaper/2018-12-28/5c258a58f1e36.jpg) no-repeat center center',
              
                backgroundAttachment:'fixed',
            },
            index:98,
            backgroundimg: 'D:/meinv/meinv1.jpg',
            value: '',
            joke: '',
            city: '',
            weatherlist: [],
            song: '',
            songlist: [],
            songurl: '',
            songer: [],
            singerurl: '',
            currsong: '',
            hotcomments: [],
            currindex: ''
            ,sonenum:133,
    };
  },
};
</script>
<style type="text/css">
        #joker {
            color: #409EFF;
            border: 0px solid;
            width: 300px;
            height: 450px;
            border-radius: 10px;
            position: absolute;
            top: 150px;
            left: 1040px;
            -webkit-box-shadow: #666 0px 0px 5px;
            -moz-box-shadow: #666 0px 0px 5px;
            box-shadow: #666 0px 0px 5px;
        }

        .ul-list li {
            list-style-type: none
        }

        .el-row {
            margin-bottom: 1px;


        }

        .el-col {
            border-radius: 4px;
        }

        .bg-purple-dark {
            background: #99a9bf;
        }

        .bg-purple {
            background: #d3dce6;
        }

        .bg-purple-light {
            background: #e5e9f2;
        }

        .white-space {
            background: white;
        }

        .grid-content {
            border-radius: 4px;
            min-height: 40px;
            -webkit-box-shadow: #67C23A 0px 0px 20px;
            -moz-box-shadow: #67C23A 0px 0px 20px;
            box-shadow: #67C23A 0px 0px 20px;
        }

        .row-bg {
            padding: 0px 0;
            background-color: #f9fafc;
        }

        .playlist {
text-align: left;
            top: 40px;

            background-image: image("images\美女图片.jpg");
            height: 450px;
            border-radius: 5px;
            overflow-x: hidden;
            overflow-y: scroll;
            -webkit-box-shadow: #666 0px 0px 0px;
            -moz-box-shadow: #666 0px 0px 0px;
            box-shadow: #666 0px 0px 20px;
            /*y轴滚动*/
        }

        .playlist::-webkit-scrollbar {
            display: none;
            /*隐藏滚动条*/
        }

        .demo {
            top: 30px;
            position: absolute;
            left: 320px;
            text-align: center;
        }

        @-webkit-keyframes rotation {

            from {
                -webkit-transform: rotate(0deg);
            }

            to {
                -webkit-transform: rotate(360deg);
            }

        }

        .an {

            -webkit-transform: rotate(360deg);

            animation: rotation 6s linear infinite;

            -moz-animation: rotation 6s linear infinite;

            -webkit-animation: rotation 6s linear infinite;

            -o-animation: rotation 6s linear infinite;

        }

        .img {
            border-radius: 250px;
        }

        .head-img {
            border-radius: 30px;
            height: 30px;
            width: 30px;
        }

        .player {
            top: 75px;
            position: absolute;
            height: 580px;
            width: 900px;
            left: 75px;
            border-radius: 5px;
            -webkit-box-shadow: #F56C6C 0px 0px 20px;
            -moz-box-shadow: #F56C6C 0px 0px 20px;
            box-shadow: #F56C6C 0px 0px 20px;
        }

        .comment {


            border-radius: 2px;
            -webkit-box-shadow: #409EFF 0px 0px 5px;
            -moz-box-shadow: #409EFF 0px 0px 5px;
            box-shadow: #409EFF 0px 0px 5px;
        }

        .bodyimage {
            /* background-image:url({{backgroundimg}});  */
            background-size: cover;
            background-attachment: fixed;
            background-color: #CCCCCC;
            width: 100%;
            height:100%;
           
        }

        
    </style>




