<template>
  <div>
    <el-container class="home_container">
      <el-header height="50px">
        <div>
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="homeUrl">
              <font class="navigation" size="3">首页</font>
            </el-breadcrumb-item>
            <el-breadcrumb-item v-show="ismyarticle" :to="myarticleUrl">
              <font class="navigation" size="2">我的文章</font>
            </el-breadcrumb-item>
            <el-breadcrumb-item v-show="ispost" :to="postUrl">
              <font class="navigation" size="2">编写文章</font>
            </el-breadcrumb-item>
          </el-breadcrumb>
        </div>

        <el-drawer
          :with-header="false"
          :visible.sync="drawer3"
          :direction="direction3"
          :size="drawerWidth3"
          :before-close="handleClose"
        >
          <div class="drawer3-all" v-show="iscomment">
            <ul v-for="item in commentsarticles" :key="item.id">
              <li @click="showarticle(item.id,item.author)">
                <div class="listarticle">
                  <div class="artheader">{{item.title}}</div>
                  <div class="artcontent">
                    <font class="navigation3" size="3">{{item.summary}}</font>
                  </div>
                  <div
                    class="arttime"
                  >作者：{{item.author}}&nbsp;&nbsp;&nbsp;&nbsp;日期：{{item.publishDate}}</div>
                </div>
              </li>
            </ul>
          </div>
          <div class="drawer3-all" v-show="!iscomment">
            <ul v-for="item in commentsarticles2" :key="item.id">
              <li @click="showarticle2(item.id,item.author)">
                <div class="listarticle">
                  <div class="artheader">{{item.title}}</div>
                  <div class="artcontent">
                    <font class="navigation3" size="3">{{item.summary}}</font>
                  </div>
                  <div
                    class="arttime"
                  >作者：{{item.author}}&nbsp;&nbsp;&nbsp;&nbsp;日期：{{item.publishDate}}</div>
                </div>
              </li>
            </ul>
          </div>
        </el-drawer>

        <div class="mytitle">YIKING-BLOG</div>
        <div class="articleList-tags">
          <div v-for="(item,index) in categories" :key="item.id" class="articleList-category">
            <div @click="deliverCategory(item.id,index)" v-show="index!=4">
              &nbsp;&nbsp;
              <font :color="colors[index]">{{item.cateName}}</font>&nbsp;&nbsp;
              <font color="#909399" size="4px">/</font>
            </div>
            <div @click="deliverCategory(item.id,index)" v-show="index==4">
              &nbsp;&nbsp;
              <font :color="colors[index]">{{item.cateName}}</font>
            </div>
          </div>
        </div>
        <el-drawer
          :with-header="false"
          :visible.sync="drawer2"
          :direction="direction2"
          :before-close="handleClose"
          :size="drawerWidth2"
        >
          <div class="mypage-background" :style="beautifulgril">
            <div @click="changeImg1" style="position: absolute; left: 5px;top:720px;">《</div>

            <div @click="changeImg2" style="position: absolute; left: 25px;top: 720px;">》</div>

            <br />
            <br />
            <div class="mypage-all">
              <font class="navigation3" size="4">
                <div class="pic2">{{currentUserName}}</div>
              </font>

              <div>
                <el-avatar :src="headerimageurl" :size="70"></el-avatar>
                <div class="mypage-avatar">
                  <el-tooltip class="item" effect="dark" :content="uploadtips" placement="bottom">
                    <el-upload
                      class="avatar-uploader"
                      action="#"
                      :show-file-list="false"
                      :on-success="handleAvatarSuccess"
                      :before-upload="beforeAvatarUpload"
                      :http-request="uploadImg"
                    >
                      <img v-if="imageUrl" :src="imageUrl" class="avatar" />
                      <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                  </el-tooltip>
                </div>
              </div>
              <hr />
              <div class="mypage-content" v-show="hasArticle">
                <font class="navigation2" size="4">发表文章数：{{myArticlesum}}</font>
                <br />
                <font class="navigation2" size="4">文章积分：{{myArticlerate}}</font>
                <br />
                <font class="navigation2" size="4">文章重复度：{{myRepeatrate}}</font>
              </div>
              <div class="mypage-content" v-show="!hasArticle">
                <font class="navigation2" size="4">您还没有文章发表！！赶紧去试试吧。</font>
              </div>
              <el-badge :value="readingcommentsum" class="item">
                <el-button size="small" @click="showcomment">评论</el-button>
              </el-badge>
              <el-badge :value="replycommentsum" class="item">
                <el-button size="small" @click="showreply">回复</el-button>
              </el-badge>
            </div>

            <div class="mypage-date">
              <!-- <Calendar :sundayStart="true"></Calendar> -->
              <mycalendar></mycalendar>
            </div>
          </div>
        </el-drawer>

        <el-dropdown @command="handleCommand">
          <el-button type="success" icon="el-icon-user">
            {{currentUserName}}
            <i class="el-icon-arrow-down el-icon--right"></i>
          </el-button>

          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="MyHome">个人主页</el-dropdown-item>

            <el-dropdown-item command="wirteMsg">编写文章</el-dropdown-item>
            <el-dropdown-item command="MyArticle">我的文章</el-dropdown-item>
            <el-dropdown-item command="logout" divided>退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-header>

      <el-container>
        <el-aside width="120px">
          <div class="home-musicplayer" @click="drawer = true"></div>

          <el-drawer
            :with-header="false"
            :visible.sync="drawer"
            :direction="direction"
            :before-close="handleClose"
            :size="drawerWidth"
          >
            <musicplayer></musicplayer>
          </el-drawer>
        </el-aside>
        <el-container>
          <el-main>
            <transition name="fade">
              <router-view :key="key"></router-view>
            </transition>
          </el-main>
        </el-container>
      </el-container>
    </el-container>
    <el-backtop :bottom="150">
      <div
        style="{
        height: 100%;
        width: 100%;
        background-color: #f2f5f6;
        box-shadow: 0 0 6px rgba(0,0,0, .12);
        text-align: center;
        line-height: 40px;
        color: #1989fa;
      }"
      >UP</div>
    </el-backtop>
  </div>
</template>
<script>

import mycalendar from "../views/MyCalendar.vue";
import { getRequest } from "../utils/api";
import { uploadFileRequest } from "../utils/api";
import musicplayer from "@/views/MusicPlayer.vue";
import { postRequest } from "../../../blog-ui-test/src/utils/api";
export default {
  components: {
    musicplayer,
   
    mycalendar,
  },
  //  props:{
  //      currArticleId:Object,
  //   },
  methods: {
    deliverCategory(id,index) {
      var _this = this;
      //console.log(this)
      for(var i=0;i<6;i++){
        _this.colors[i]="white";
      }
      _this.colors[index]="yellow";
      _this.$router.replace({
        path: "/home/"+_this.currentUserName+"/listArticle/yiking" + id + "/0/1",
      });
      //console.log(id);
    },
    showcomment() {
      var _this = this;

      _this.drawer3 = true;
      _this.iscomment = true;
    },
    showreply() {
      var _this = this;

      _this.drawer3 = true;
      _this.iscomment = false;
    },
    uploadImg(f) {
      var _this = this;
      var formdata = new FormData();
      formdata.append("image", f.file);
      formdata.append(" username", _this.currentUserName);

      uploadFileRequest("/updateheadimg", formdata).then(function (resp) {
        if (resp.data.status == "success") {
          _this.$confirm("头像更新成功", "刷新试试吧！", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning",
          });
        }
      });
    },
    handleAvatarSuccess(res, file) {
      //console.log(res);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
     // console.log(file);
      return isJPG && isLt2M;
    },
    handleClose(done) {
      done();
    },
    handleCommand(command) {
      var _this = this;
      if (command == "logout") {
        this.$confirm("注销登录吗?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }).then(
          function () {
            getRequest("/logout");
            _this.currentUserName = "游客";
            _this.$router.replace({ path: "/" });
          },
          function () {
            //取消
          }
        );
      } else if (command == "MyHome") {
        _this.drawer2 = true;
        getRequest("/article/getuserdata/" + _this.currentUserName).then(
          function (msg) {
           // console.log(msg);
            _this.myArticlesum = msg.data.articlesum;
            _this.myArticlerate = msg.data.articlesrate;
            _this.myRepeatrate = msg.data.userrepeatrate;
            _this.headerimageurl = msg.data.headerimage;
            _this.readingcommentsum = msg.data.readingcommentsum;
            _this.replycommentsum = msg.data.replycommentsum;
            _this.commentsarticles = msg.data.commentsarticles;
            _this.commentsarticles2 = msg.data.commentsarticles2;
            _this.beautifulgril.background =
        "url(http://localhost:8003/image/background" +
       msg.data.userbackground +
        ".jpg) no-repeat center center";
            if (_this.myArticlesum > 0) {
              _this.hasArticle = true;
            } else {
              _this.hasArticle = false;
            }
            // console.log(_this.commentsarticles);
          },
          function () {
           // console.log("获取失败");
          }
        );
      } else if (command == "wirteMsg") {
        _this.$router.push({
          path: "/home/" + _this.currentUserName + "/postArticle",
        });
        _this.postUrl = "/home/" + _this.currentUserName + "/postArticle";
        _this.ispost = true;
      } else if (command == "MyArticle") {
        _this.myarticleUrl =
          "/home/" + _this.currentUserName + "/listArticle/myarticles/0/1";
        _this.$router.push({
          path:
            "/home/" + _this.currentUserName + "/listArticle/myarticles/0/1",
        });
        _this.ismyarticle = true;
      }
    },
    changeImg1: function () {
      var that = this;
      if (that.index == 1) {
        that.index = 8;
      } else {
        that.index--;
      }
      that.beautifulgril.background =
        "url(http://localhost:8003/image/background" +
        that.index +
        ".jpg) no-repeat center center";
        postRequest("/updatebackground",{
          username:that.currentUserName,
          background: that.index 
        }
        )
    },
    changeImg2: function () {
      var that = this;
      //var index=parseInt(that.beautifulgril.background.substring(18,20));
      if (that.index == 8) {
        that.index = 1;
      } else {
        that.index++;
      }
      that.beautifulgril.background =
        "url(http://localhost:8003/image/background" +
        that.index +
        ".jpg) no-repeat center center";
         postRequest("/updatebackground",{
          username:that.currentUserName,
          background: that.index 
        }
        )
    },
    showarticle: function (id, author) {
      var _this = this;
      postRequest("/comments/updatecomments", {
        aid: id,
        username: _this.currentUserName,
      }).then(function (resp) {
        //console.log(resp);
        _this.$router.push({
          path:
            "/home/" +
            _this.$route.params.username +
            "/listArticle/article/" +
            id +
            "/" +
            author +
            "/" +
            0,
        });
        _this.drawer3 = false;
        _this.drawer2 = false;
      });
    },
    showarticle2: function (id, author) {
      var _this = this;
      postRequest("/comments/updatereply", {
        aid: id,
        username: _this.currentUserName,
      }).then(function (resp) {
        //console.log(resp);
        _this.$router.push({
          path:
            "/home/" +
            _this.$route.params.username +
            "/listArticle/article/" +
            id +
            "/" +
            author +
            "/" +
            0,
        });
        _this.drawer3 = false;
        _this.drawer2 = false;
      });
    },
  },

  computed: {
    key() {
      return this.$route.name !== undefined
        ? this.$route.name + new Date()
        : this.$route + new Date();
    },
  },
  mounted: function () {
    var _this = this;
   
    getRequest("/currentUserName").then(
      function (msg) {},
      function (msg) {
        _this.currentUserName = "游客";
        _this.$router.replace({ path: "/" });
       
      }
    );
     getRequest("/getcategories").then(function (msg) {
    //  console.log(msg);
      _this.categories = msg.data;
      for (var i = 0; i < 5; i++) {
        _this.cateUrl[0] =
          "/home/yiking/listArticle/yiking" + _this.categories[0].id + "/0/1";
      }
      
    }, function (msg) {
        _this.currentUserName = "游客";
        _this.$router.replace({ path: "/" });
       
      });

    if (_this.$route.params.username == "yi") {
      getRequest("/currentUserName").then(
        function (msg) {
          _this.currentUserName = msg.data;
          _this.$router.replace({
            path:
              "/home/" + _this.currentUserName + "/listArticle/allarticles/0/1",
          });
          _this.homeUrl =
            "/home/" + _this.currentUserName + "/listArticle/allarticles/0/1";
         // console.log(_this.homeUrl);
        },
        function (msg) {
          
          _this.currentUserName = "游客";
          _this.$router.replace({ path: "/" });
        }
      );
    } else {
      _this.currentUserName = _this.$route.params.username;
      _this.homeUrl =
        "/home/" +
        _this.$route.params.username +
        "/listArticle/allarticles/0/1";
      //console.log(_this.homeUrl);
    }
    if (_this.$route.path == "/home") {
      _this.$router.push({
        path:
          "/home/" +
          _this.$route.params.username +
          "/listArticle/allarticles/0/1",
      });
    }
  },
  data() {
    return {
      beautifulgril: {
        background:
          "url(http://localhost:8003/image/background3.jpg) no-repeat center center",
      },
      index1: "",
      hasArticle: true,
      index: 3,
      uploadtips: "点击此处上传头像！",
      homeUrl: "",
      imageUrl: "",
      myarticleUrl: "",
      postUrl: "",
      drawer: false,
      direction: "ltr",
      drawer2: false,
      direction2: "rtl",
      drawer3: false,
      direction3: "ltr",
      drawerWidth2: "20%",
      drawerWidth3: "70%",
      currentUserName: "",
      currarticleid: "123",
      drawerWidth: "80%",
      ismyarticle: false,
      ispost: false,
      myArticlesum: "",
      myArticlerate: "",
      myRepeatrate: "",
      headerimageurl:
        "http://localhost:8003/image/2dc38b56-7c74-4ef8-8216-1eadbb3bfe2a_meinv119.jpg",
      headerimagesize: "50px",
      datevalue: new Date(),
      value: "",
      iscomment: "",
      readingcommentsum: "",
      replycommentsum: "",
      commentsarticles: [],
      commentsarticles2: [],
      commentsarticlestemp: [],
     
      categories: {
        cateName: "",
        id: "",
      },
      cateUrl: [],
      colors: ["white","white","white","white","white","white"],
    };
  },
};
</script>
<style>
.fade-enter {
  opacity: 0;
}
.fade-leave-to {
  display: none;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.2s ease;
}
.fade-enter-to,
.fade-leave {
  visibility: visible;
  opacity: 1;
}
.mypage-all {
  width: 100%;
  text-align: center;
}
.el-dropdown {
  vertical-align: top;
}
.el-dropdown + .el-dropdown {
  margin-left: 15px;
}
.el-icon-arrow-down {
  font-size: 12px;
}
.el-row {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
}
.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  text-align: center;
  line-height: 40px;
  border-radius: 4px;
  min-height: 36px;
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}
.article {
  height: 600px;
  width: 950px;
}
.home_container {
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
}
.navigation {
  color: white;
}
.mypage-content {
  text-align: left;
  margin: 30px;
}
.navigation2 {
  color: #20a0ff;
}
.navigation3 {
  color: black;
}

.el-header {
  background-color: #20a0ff;
  color: white;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: space-between;

  -webkit-box-shadow: #20a0ff 0px 0px 1px;
  -moz-box-shadow: #f56c6c 0px 0px 1px;
  box-shadow: #20a0ff 0px 1px 5px 1px;
  border-radius: 10px;
}

.el-aside {
  background-color: white;
}

.el-main {
  background-color: #fff;
  color: #000;
  text-align: center;
}
.mytitle {
  width: 10%;
  left: 500px;
  position: absolute;

  text-align: center;
}
.item {
  margin-top: 10px;
  margin-right: 40px;
  border: 0px;
}
.home-musicplayer {
  text-align: center;

  position: fixed;
  margin-top: 250px;
  margin-left: 15px;
  height: 70px;
  width: 70px;
  background: url(https://www.easyicon.net/api/resizeApi.php?id=1069493&size=96)
    no-repeat center center;
  -webkit-box-shadow: #409eff 0px 0px 1px;
  -moz-box-shadow: #409eff 0px 0px 1px;
  box-shadow: #409eff 0px 1px 10px 1px;
  border-radius: 35px;
}
.mypage-background {
  height: 100%;
  width: 100%;
  overflow: hidden;
  background: url(http://localhost:8003/image/background3.jpg) no-repeat center
    center;
}
.mypage-avatar {
  margin-left: 145px;
  margin-top: -6px;
  width: 20px;
  height:20px;
  border: 0px;
}
.mypage-date {
  position: absolute;
  top: 465px;
  width: 100%;
  height: 250px;
  padding-left:30px;
  text-align: center;

overflow: hidden;
 
}

.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 15px;
  color: #8c939d;
  width: 15px;
  height: 15px;
  line-height: 15px;
  text-align: center;
}
.avatar {
  width: 15px;
  height: 15px;
  display: block;
}

.pic2 {
  width: 200px;
  height: 30px;
  margin: 2px auto;
  background: url(http://localhost:8003/image/background.jpg) no-repeat center
    center;
  background-size: cover;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  line-height: 30px;
  /*很重要*/
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.listarticle {
  width: 1000px;
  padding: 15px;
  height: 60px;
  -webkit-box-shadow: #666 0px 0px 1px;
  -moz-box-shadow: #666 0px 0px 1px;
  box-shadow: #c0c4cc 0px 1px 1px 1px;
  border-radius: 5px;
}
.listarticle:hover {
  background: #f2f6fc;
}
.artheader {
  color: #20a0ff;
  width: 100%;
  text-align: center;
  font-size: 18px;
}
.arttime {
  width: 100%;
  text-align: right;
  font-size: 13px;
  color: #f56c6c;
}
.artcontent {
  width: 95%;
  text-align: left;
  font-size: 15px;
}
.drawer3-all {
  width: 100%;
}
.articleList-tags {
  left: 900px;
  position: absolute;
  margin-top: 5px;

  text-align: center;
}
.articleList-category {
  display: inline-block;
}
</style>




