import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from '@/components/Login'
import PostArticle from '@/views/PostArticle.vue'
import ArticleList from '@/views/ArticleList.vue'
import Article from '@/views/Article.vue'
import MusicPlayer from "@/views/MusicPlayer.vue";
import SaveUser from "@/views/SaveUser.vue"
import Register from "@/components/Register.vue"
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Login',
    component: Login,
    hidden: true

  },
  {
    path: '/reg',
    name: 'Register',
    component: Register,
    hidden: true

  },
  {
    path: '/home/saveuser',
    name: ' SaveUser',
    component:  SaveUser
  },
  {
    path: '/home/:username',
    name: 'Home',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: Home,
    children: [
      {
        path: '/home/:username/postarticle',
        name: 'postArticle',
        component: PostArticle
      },

      {
        path: '/home/musicplayer',
        name: 'musicplayer',
        component: MusicPlayer
      },{
        path:'/home/:username/listArticle/:article/:search/:page',
        name:'articleList',
        component:ArticleList,
        // children:[{
        //   path: '/home/articlewordcloud',
        //   name: 'articlewordcloud',
        //   component: ArticleWordCloud,
        // },]
      },{
          path:'/home/:username/listArticle/article/:currid/:author/:page',
          name:'article',
          component:Article,
        }]
  }
]

const router = new VueRouter({
  routes
})

export default router



